
// Formulario login

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.usuarios_login{
    margin-top: v.$separacion;


    .campo_login {
        margin: 1rem 0  0 0;
        padding: 0rem 1rem 0 1rem;
        border-radius: 1rem;
        border: solid 1px darken(c.$gris, 10);
        display: flex;
        align-items: center;
        label{
            flex: 0 0 10rem;
            width: 100rem;
        }

        input{
            flex: 1;
            border: none;
            padding: calc(v.$separacion / 4);
            border-radius: 10px;
        }

    }

}