
// Calificaciones - blog

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;


.califica{
  margin-top: 10rem;
  border: solid 1px darken(c.$gris, 5);
  padding: 1rem;
  

  .calificaciones{
    display: block;


    @include m.tablet{
      display: grid;
      gap: 2rem;
      grid-template-columns: 2fr 1fr;
    }

    .peque√±a{
      .calificacion{
        margin: 0;
        padding: 0;

        .cabecera{
          display: grid;
          gap: 2rem;
          grid-template-columns: 15% 85%;
        
          .izquierda{
            padding: 0;
            margin: 0;
            img{
              width: 10rem;
            }
          }
       

          p{
            font-size: 1.5rem;
          }
          .checked{
            color: c.$amarillo;
          }
        }

        .cuerpo{
          p{
            font-size: 1.3rem;
            margin: 0;
            line-height: 1.4;
              &::after{
                content: '';
                display: block;
                width: 100%;
                height: 0.4rem;
                background-color: c.$verde;
                margin-top: 1rem;
            }
          }
        }
      }
    }


    .grande{

      .calificacion{
        background-color: c.$verde;
        border-radius: 1rem;
        width: 100%;
        height: 100%;
        position: relative;
        padding: 1rem;
        margin: 0 auto;
  
        &::before{
          content: '';
          background-image: url('../img/comilla.svg');
          background-repeat: no-repeat;
          width: 6rem;
          height: 8rem;
          display: block;
          position: absolute;
          left: 0.5rem;
        }
  
          .cabecera{
            display: block;
            padding: 0.1rem 2rem 0rem 5rem;

            img{
              margin-left: 2rem;
              width: 12rem;
            }
            
            h4{
              color: c.$blanco;
              font-size: 2rem;
            }
            p{
              font-size: 1.5rem;
            }
            
            .checked{
              color: c.$amarillo;
            }
          }

          .cuerpo{
            padding: 2rem;
            p{
              color: c.$blanco;
              font-size: 2rem;
            }
          }
         

        }
      }
    }

    .contenedor_botones{
      margin-top: 2rem;
      a{
        margin: 0.2rem;
        padding: 2;
      }
    }
}

