// Nueva categoria

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.contenedor_formulario{
    label{
        font-weight: t.$bold;
        text-transform: uppercase;
        display: block;
        margin: 1rem;
        text-align: start;
    }
    
    input:not([type='submit']),
    textarea{
        padding: 1rem;
        display: block;
        width: 100%;
        background-color:  c.$blanco;
        border: solid 1px c.$gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }
}
