
// Para aplicar a todos los formularios por igual

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.crear_nuevo_formulario{
    margin-top: v.$separacion;

    .head_formulario{
        border: solid 1px darken(c.$amarillo, 10);
        border-radius: 1rem;
        padding: 2rem;
        margin: -1rem;
        
        .campo {
            margin: 1rem 0  0 0;
            padding: 0rem 1rem 0 1rem;
            border-radius: 1rem;
            border: solid 1px darken(c.$gris, 10);
            display: flex;
            align-items: center;
            text-align: start;

            label{
                flex: 0 0 10rem;
                width: 100rem;
            }

            input{
                flex: 1;
                border: none;
                padding: calc(v.$separacion / 4);
                border-radius: 10px;
            }

            textarea{
                border: none;
            }

        }
        .boton_verde{
            margin-bottom: 0;
            @include m.tablet{
                display: flex;
                justify-content: flex-start;
            }

        }
    }
}