
// Nosotros

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;

.nosotros{
    margin-top: v.$separacion;
    
    .historia, .equipo, .equipos{
        text-align: center;
    }

    .contenido_historia, .contenido_equipo, .contenido_equipos{

        @include m.tablet{
            display: grid;
            grid-template-columns: repeat(3, 1fr);

            .texto{
                grid-column: 2 / 3;
            }
            .imagen_uno{
                grid-row: 1 / 2;
                grid-column: 1 / 2;
            }
            .imagen_dos{
                grid-row: 1 / 2;
                grid-column: 3 / 4;
            }
        
        }
    
        p{
            text-align: justify;
            padding: 2rem;
        }

        img{
            width: 35rem;
            height: 30rem;
        }

    }

}