
// Carta general

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;

.carta_general{
    margin-top: v.$separacion;
    
    .tabla_categorias{

        @include m.tablet{
            display: grid;
            grid-template-columns: repeat(3, 1fr);
        }

        .tabla_cate{
            padding: 1rem;
            margin: 0;

            li{
                img{
                    width: 30rem;
                    height: 25rem;
                }
            }
        }
        h4{
            color: c.$amarillo;
        }
        
    }

}

