@use 'variables' as v;
@use 'colores' as c;
@use 'tipografia' as t;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}

body {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    background-color: c.$blanco;
    color: c.$amarillo;
    font-family: t.$fuente_principal;
    font-size: 1.6rem;
}
*, *:before, *:after {
    box-sizing: inherit;
}

.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}

.contenedor_tablas {
    width: 100%;
    max-width: 1300px;
    margin: 0 auto;
}


a {
    text-decoration: none;
}
img {
    max-width: 100%;
}

ul{
    list-style: none;
}


// Globales de formulario
.formulario {
    margin-top: v.$separacion;
    padding: 0 1rem 0 1rem;
    width: 100%;
}
input{
    margin: 0;
    padding: 0;
}