// Ofertas

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;

.contenedor_ofertas{
    margin-top: v.$separacion;

    .botones{
        text-align: left;

        a{
            padding: 1rem ;
            margin-bottom: 1rem;
        }
    }

    .tabla_oferta{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;

    }

    .tabla_a{
        background-color: c.$pink;
        padding: 0;
        margin: 0;
    }
    .seleccion{
        text-align: center;
        padding: 1rem;
        color: c.$blanco;
        -webkit-box-shadow: 0px 7px 19px -3px rgba(0,0,0,0.75);
        -moz-box-shadow: 0px 7px 19px -3px rgba(0,0,0,0.75);
        box-shadow: 0px 7px 19px -3px rgba(0,0,0,0.75);

        h4,
        p,
        h6{
            margin: 1rem 0 0 0;
        }

        p{
            text-align: left;
            font-size: 1.2rem;
            padding: 0 1rem 0 1rem;
        }
    }
}