
// darkMode

@use 'variables' as v;
@use 'colores' as c;

.dark_mode{
    background-color: darken(c.$grisOscuro, 10);

    p{
        color: c.$blanco;
    }
    a, h1, h2, h3, h4{
        color: c.$gris;
    }


    .anuncio,
    .resumen_propiedad
    {
        background-color: c.$grisOscuro;
        border: solid 1px c.$grisOscuro;

        .precio{
            color: c.$verde;
        }

        .icono{
            filter: invert(100%);
        }
    }

    .texto_nosotros blockquote{
        color: c.$blanco;
    }

    .formulario{

        label,
        p,
        legend{
            color: c.$gris;
        }

        input:not([type="submit"]),
        textarea,
        select{
            background-color: c.$grisOscuro;
            border: solid 1px c.$grisOscuro;
            color: c.$blanco;
        }
    }

}