// Utilidades

@use 'variables' as v;


.seccion {
    margin-top: calc(v.$separacion / 2);
    margin-bottom: calc(v.$separacion / 2);
}
.contenido_centrado {
    max-width: 80rem;
    text-align: center;
}

.alinear_derecha{
    display: flex;
    justify-content: flex-end;
}
.alinear_izquierda{
    display: flex;
    justify-content: flex-start;
}
.w-100{
    width: 100%;
}