

// Listado de cartas

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.listado_cartas{
    margin-top: v.$separacion;

    .boton{
        text-align: left;
        a{
            margin: 0;
            padding: 0, 0, 1rem, 1rem;
            border-radius: 1rem;
        }
    }
    .listado{
        @include m.tablet{
            display: grid;
            grid-template-columns: repeat(2, 1fr);
        }
        .tabla_productos{
            margin: 0.5rem;
            padding: 0;
        }

        .base{
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            height: 100%;
            padding: 1rem;
            border: 1px solid darken(c.$amarillo, 10);
            border-radius: 1rem;
            box-shadow: 1px 12px 9px -13px rgba(0, 0, 0,.4);
            .imagen{
                width: 15rem;
            }

            .texto{
                display: grid;
                grid-template-rows: 1fr, 2fr, 1fr;
                
                h4{
                    text-align: center;
                    margin: 0;
                    padding: 0;
                    font-size: 1.5rem;
                }
            
                p{
                    font-size: 1.2rem;
                    margin: 0;
                    padding: 0;
                    text-align: left;
                    align-items: start;
                }
             
                .contenedor_precios{
                    display: flex;
                    justify-content: space-between;
                    margin: 0;
                    padding: 0;
                    text-align: center;
                    h5{
                        margin: 0;
                        padding: 0.5rem 0 0 0 ;
                        font-size: 1rem;
                    }
                }
            }
        }
  
    }
}