
// Todas las calificaciones

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.contenedor_blog{
    margin-top: v.$separacion;

    .botones{
        display: block;
        margin: 2rem 0 2rem 0;

        a{
        margin: 0.2rem;
        padding: 2;
        }

        @include m.tablet{
            display: flex;
        }
    }

    .contenido_blog{
        //background-color: darken(c.$blanco, 10);
        border: solid 3px darken(c.$blanco, 7);
        border-style: outset;
    }

    .comentarios{
        margin: 0;
        padding: 0;
        display: block;

        .comentario{
            display: grid;
            grid-template-columns: 1fr 4fr;
            margin: 0;
            padding: 0;
            border-top: solid 1px darken(c.$gris, 15);
            border-bottom: solid 1px darken(c.$gris, 15);
            background-color: c.$lime-50;

            .columnas_I{
                text-align: center;
                padding: 1.5rem;
                border-right: solid 1px darken(c.$gris, 15);
                img{
                    width: 10rem;
                }
                h5{
                    font-size: 1.5rem;
                    padding: 1rem 0 0 0;
                    margin: 0;
                }
                p{
                    font-size: 1rem;
                }

            }

            .columnas_D{
                display: block;
                padding: 1.5rem;
                
                .clasificacion_pagina{
                    margin: 0;
                    font-size: 1.5rem;
                }

                h5{
                    font-size: 1.5rem;
                    padding: 1rem 0 0 0;
                    margin: 0;
                }

                p{
                    font-size: 1.1rem;
                }
            }

            // Estrellas
            .checked{
                color: c.$amarillo;
            }
        }


    }

}
