// Dejar rese√±as

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.dejar{
    margin-top: v.$separacion;
    
    p{
        font-size: 1.6rem;
        color: c.$grisOscuro;
        margin: 2rem 0 2rem 0;
        text-align: center;
        
    }

    legend{
        font-size: 2rem;
        color: c.$grisOscuro;
    }

    label{
        font-weight: t.$bold;
        text-transform: uppercase;
        display: block;
        margin: 1rem;
    }

    input:not([type='submit']),
    textarea{
        padding: 1rem;
        display: block;
        width: 100%;
        background-color:  c.$blanco;
        border: solid 1px c.$gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }

   
}

.contenedor_formulario{
    border: solid 1px darken(c.$gris, 10);
    padding: 2rem;

}

// estrellas
.comentarios{
    .clasificacion{
        display: flex;
        direction: rtl;
        unicode-bidi: bidi-override;
        width: 25rem;

        input[type="radio"] {
            display: none;
        }

        label{
            background-color: darken(c.$blanco, 10);
            border: solid 1px darken(c.$blanco, 7);
            color: c.$gris;
            text-shadow: 1px 1px rgba(0,0, 0, 3);
            padding: 1rem;
            margin: 0;
            font-size: 3rem;
        }
        label:hover,
        label:hover ~ label {
          color: orange;
        }
        
        input[type="radio"]:checked ~ label {
          color: orange;
        }
    }
   

}